*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}
/* FONDO NAVBAR */
.custom-navbar{
    background-color: var(--surface) !important;
}

.logo_peluqueria{ /* Renamed to logo_fitter in nav.html, but keeping class name for now */
    width: 65px;
    height: 65px;
}



/* BODY(IMAGEN DE FONDO) */
body {
    /* Changed background image to a gym-related one */
    background-image: linear-gradient(rgba(255, 255, 255, 0.62),rgba(255, 255, 255, 0.62)),url("data:image/svg+xml;utf8,<svg%20xmlns=http://www.w3.org/2000/svg%20width=240%20height=80><rect%20width=240%20height=80%20fill=%231a1a1a/><text%20x=120%20y=45%20fill=white%20font-size=32%20font-family=Arial%20dominant-baseline=middle%20text-anchor=middle>FITTER</text></svg>"); /* fallback inline SVG */
    background-size:cover; /* Changed to cover for better gym image display */
    background-position: center; /* Centra la imagen */
    background-repeat: no-repeat; /* Changed to no-repeat */
    
}
.nav-link{
    color: gainsboro;
}

.title-form{
    padding: 10px;
}
/* FOOTER */

.footer{
    background-color: var(--surface) !important;
    padding: 50px 0; /* primero es vertical y el segundo es horizontal */
    
}

.container-footer{
    width: 100%;
    margin: 0 auto;
    
}

.footer-row{
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
    
}

.footer-links{
    width: 30%;
    padding: 0 15px;
}

.footer-links h4{
    font-size: 20px;
    color: white;
    margin-bottom: 25px;
    font-weight: 500;
    border-bottom: 2px solid #4CAF50; /* Green accent for gym theme */
    padding-bottom: 10px;
    display: inline-block;
}

.footer-links ul li a{
    font-size: 18px;
    text-decoration: none;
    color: gainsboro;
    display: block;
    margin-bottom: 7px;
    transition: all 0.5s ease;
    text-align: center;
}

.footer-links ul li a:hover{
    color: white;
    padding-left: 3px;
}

.footer-links ul{
    padding: 0px;
}

.social-links a{
    display: inline-block;
    min-height: 40px;
    width: 40px;
    background-color: rgba(255, 255, 255, 0.2);
    margin: auto 7px ;
    text-align: center;
    line-height: 40px;
    border-radius: 50%;
    color: white;
    
}

@media(max-width:767px){
    .footer-row{
        text-align: center;
    }

    .footer-links{
        width: 100%;
        margin-bottom: 30px;
        
    }
    
    
}
